<script>
  cast.receiver.logger.setLevelValue(cast.receiver.LoggerLevel.DEBUG);

  const namespace = "urn:x-cast:es.offd.gnu";
  let url = "[ No url ]";
  function main() {
    function setStatus(msg) {
      if (msg) {
        status.classList.remove("empty");
      } else {
        status.classList.add("empty");
      }
      status.textContent = msg;
    }

    window.castReceiverManager = cast.receiver.CastReceiverManager.getInstance();
    castReceiverManager.onReady = function(event) {
      window.castReceiverManager.setApplicationState("Application ready");
      setStatus("Waiting for address");
    };

    // create a CastMessageBus to handle messages for a custom namespace
    window.messageBus = window.castReceiverManager.getCastMessageBus(namespace);

    // handler for the CastMessageBus message event
    window.messageBus.onMessage = function(event) {
      var msg = JSON.parse(event.data);
      if (msg.url) {
        url = msg.url;
      }
      window.messageBus.send(event.senderId, event.data);
    };

    // initialize the CastReceiverManager with an application status message
    window.castReceiverManager.start({ statusText: "Application is starting" });
  }

  try {
    main();
  } catch (e) {
    console.log(e);
  }
</script>

<style>
  #main {
    margin: 0;
    padding: 0;
    width: 100%;
    height: 100%;
  }
</style>

<div id="main" class="fade">
  <h1>En gnu!!</h1>
  <h1>:D</h1>
  <div>{url}</div>
</div>
<div id="status" class="empty" />
